@keyframes waves {
  0% {
    border-radius: 30% 70% 70% 30%/30% 30% 70% 70%; }
  25% {
    border-radius: 70% 30% 30% 70%/70% 70% 30% 30%; }
  50% {
    border-radius: 30% 70% 70% 30%/30% 30% 70% 70%; }
  100% {
    border-radius: 70% 30% 30% 70%/70% 70% 30% 30%; } }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

body {
  align-items: center;
  background-color: #2C2141;
  background-image: linear-gradient(135deg, #FEE140 0%, #FA709A 100%);
  display: flex;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  justify-content: center;
  min-height: 100vh;
  padding: 16px; }
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    body {
      /* IE10+ CSS styles go here */
      height: 100vh; } }

.card {
  background-color: #F8F8F8;
  border-radius: 16px;
  max-width: 624px;
  padding: 32px;
  width: 100%; }

.person {
  align-items: flex-start;
  display: flex; }
  .person__bio {
    line-height: 22px;
    margin: 0; }
  .person__distinction {
    color: #ED6D05;
    font-weight: normal; }
  .person__header {
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 28px; }
    @media screen and (max-width: 514px) {
      .person__header {
        text-align: center; } }
  .person__link {
    color: #3BB9EB;
    text-decoration: none; }
  .person__name {
    font-size: 36px;
    margin: 0;
    font-weight: normal; }
  .person__title {
    color: #545454;
    font-size: 18px;
    font-style: italic;
    font-weight: normal;
    -webkit-text-fill-color: transparent; }
    @supports (-webkit-background-clip: text) {
      .person__title {
        background: linear-gradient(to right, #FEE140, #FA709A);
        background-clip: text;
        box-decoration-break: clone;
        -webkit-text-fill-color: transparent; } }
  @media screen and (max-width: 514px) {
    .person {
      flex-wrap: wrap;
      justify-content: center; } }

.photo {
  margin: 0 32px 16px 0;
  position: relative; }
  .photo__img {
    animation: waves 30s infinite alternate ease-in-out;
    border-radius: 30% 70% 70% 30%/30% 30% 70% 70%;
    height: 148px;
    width: 148px;
    will-change: border-radius; }
  @media screen and (max-width: 514px) {
    .photo {
      margin: 0; } }
